import React, { useEffect } from "react";
import { v4 as uuidv4 } from "uuid";

var audioObj = new Audio(
  "https://taira-komori.jpn.org/sound_os/daily02/pushing_a_key.mp3"
);
const replay = () => {
  window.location.reload();
};

const HintIcon = ["O", "X"];
let arrLists = ["", "", "", "", "", "", "", "", ""];
let NowSign = "";
let NowPosNo;
let WinPos = [];
const PlayBoard = ({ count, setCount, winMember, setWinMember, GameEnd }) => {
  const SelectZone = (e) => {
    let iNo = e.target.getAttribute("data-no");
    if (arrLists[iNo] !== "") return;
    let HintTxt = HintIcon[count % 2];
    arrLists[iNo] = HintTxt;
    NowSign = HintTxt;
    NowPosNo = parseInt(iNo);

    e.target.innerHTML = HintTxt;
    setCount(count + 1);

    const playPromise = audioObj.play();
    if (playPromise !== undefined) {
      playPromise
        .then((_) => {
          //
        })
        .catch((error) => {
          console.log("playback prevented");
        });
    }
  };

  const chechWin = () => {
    // 比橫排
    if (NowPosNo % 3 === 0) {
      if (
        arrLists[NowPosNo + 1] === NowSign &&
        arrLists[NowPosNo + 2] === NowSign
      ) {
        WinPos.push(NowPosNo, NowPosNo + 1, NowPosNo + 2);
        setWinMember(NowSign);
        return;
      }
    } else if (NowPosNo % 3 === 1) {
      if (
        arrLists[NowPosNo - 1] === NowSign &&
        arrLists[NowPosNo + 1] === NowSign
      ) {
        WinPos.push(NowPosNo, NowPosNo - 1, NowPosNo + 1);
        setWinMember(NowSign);
        return;
      }
    } else {
      if (
        arrLists[NowPosNo - 1] === NowSign &&
        arrLists[NowPosNo - 2] === NowSign
      ) {
        WinPos.push(NowPosNo, NowPosNo - 1, NowPosNo - 2);
        setWinMember(NowSign);
        return;
      }
    }

    // 比直排
    if (Math.floor(NowPosNo / 3) === 0) {
      if (
        arrLists[NowPosNo + 3] === NowSign &&
        arrLists[NowPosNo + 6] === NowSign
      ) {
        WinPos.push(NowPosNo, NowPosNo + 3, NowPosNo + 6);
        setWinMember(NowSign);
        return;
      }
    } else if (Math.floor(NowPosNo / 3) === 1) {
      if (
        arrLists[NowPosNo + 3] === NowSign &&
        arrLists[NowPosNo - 3] === NowSign
      ) {
        WinPos.push(NowPosNo, NowPosNo + 3, NowPosNo - 3);
        setWinMember(NowSign);
        return;
      }
    } else {
      if (
        arrLists[NowPosNo - 6] === NowSign &&
        arrLists[NowPosNo - 3] === NowSign
      ) {
        WinPos.push(NowPosNo, NowPosNo - 6, NowPosNo - 3);
        setWinMember(NowSign);
        return;
      }
    }

    // 比斜排
    if ([0, 2, 4, 6, 8].indexOf(NowPosNo) >= 0) {
      if (
        arrLists[0] === NowSign &&
        arrLists[4] === NowSign &&
        arrLists[8] === NowSign
      ) {
        WinPos.push(0, 4, 8);
        setWinMember(NowSign);
        return;
      }

      if (
        arrLists[2] === NowSign &&
        arrLists[4] === NowSign &&
        arrLists[6] === NowSign
      ) {
        WinPos.push(2, 4, 6);
        setWinMember(NowSign);
        return;
      }
    }
  };

  useEffect(() => {
    if (count >= 5) {
      chechWin();
    }
    if (count === 9) {
      setTimeout(() => {
        GameEnd();
      }, 500);
    }
  }, [count]);

  useEffect(() => {
    if (winMember !== "") {
      setTimeout(() => {
        GameEnd();
      }, 1500);
    }
  }, [winMember]);

  const showHint = (e) => {
    let iNo = e.target.getAttribute("data-no");
    if (arrLists[iNo] !== "") return;

    let HintTxt = HintIcon[count % 2];
    e.target.innerHTML = HintTxt;
    e.target.classList.add("HintFontColor");
  };

  const cancelHint = (e) => {
    let iNo = e.target.getAttribute("data-no");
    if (arrLists[iNo] !== "") return;

    e.target.innerHTML = "";
    e.target.classList.remove("HintFontColor");
  };

  let lists = arrLists.map((list, index) => (
    <div
      key={uuidv4()}
      onMouseEnter={winMember === "" ? showHint : null}
      onMouseLeave={winMember === "" ? cancelHint : null}
      onClick={winMember === "" ? SelectZone : null}
      className={
        "divSelectZone " +
        (list === "X" ? "Color-X" : "Color-O") +
        " " +
        (WinPos.indexOf(index) >= 0 ? "FlashAnimation" : "")
      }
      data-no={index}
    >
      {list}
    </div>
  ));

  return (
    <div className="divMainGame">
      {lists}
      <label onClick={replay} className="btnReplay visible-hidden">
        REPLAY
      </label>
    </div>
  );
};

export default PlayBoard;
