import React, { useState } from "react";
import PlayerInfo from "./component/PlayerInfo";
import PlayBoard from "./component/PlayBoard";
import $ from "jquery";

function App() {
  const [count, setCount] = useState(function () {
    return 0;
  });

  const [winMember, setWinMember] = useState(function () {
    return "";
  });

  const GameEnd = () => {
    $(".btnReplay").removeClass("visible-hidden");
  };

  return (
    <div className="App">
      <header className="App-header">
        <PlayBoard
          count={count}
          setCount={setCount}
          winMember={winMember}
          setWinMember={setWinMember}
          GameEnd={GameEnd}
        />
        <PlayerInfo dataNo={count} winMember={winMember} />
      </header>
    </div>
  );
}

export default App;
