import React, { Fragment } from "react";

const PlayerInfo = ({ dataNo, winMember }) => {
  let ClassName_Left = dataNo % 2 === 0 ? "" : "visible-hidden";
  let ClassName_Right = dataNo % 2 === 0 ? "visible-hidden" : "";
  let ClassWinName_Left = winMember === "O" ? "" : "visible-hidden";
  let ClassWinName_Right = winMember === "X" ? "" : "visible-hidden";

  if (ClassWinName_Left === "" || ClassWinName_Right === "" || dataNo === 9) {
    ClassName_Left = "visible-hidden";
    ClassName_Right = "visible-hidden";
  }

  return (
    <Fragment>
      <div className="Member_Left">
        <i className="bi bi-person-fill"></i>
        <span className="MemberNo_Left"> Player 1</span>
        <i
          className={"bi bi-caret-left-fill arrow arrow_left " + ClassName_Left}
        ></i>
        <i className={"bi bi-trophy-fill" + ClassWinName_Left}></i>
      </div>
      <span className={"Member_Left_Win " + ClassWinName_Left}>Win</span>
      <span className={"Member_Right_Win " + ClassWinName_Right}>Win</span>
      <div className="Member_Right">
        <i className={"bi bi-trophy-fill" + ClassWinName_Right}></i>
        <i
          className={
            "bi bi-caret-left-fill arrow arrow_right " + ClassName_Right
          }
        ></i>
        <i className="bi bi-person-fill"></i>
        <span className="MemberNo_Left"> Player 2</span>
      </div>
    </Fragment>
  );
};

export default PlayerInfo;
